:root {
  --logo-color: #fb6620;
}


.line {
  width: 100%;
  background: #000000;
  height: 1px;
}

.fixed {
  position: fixed;
  margin-top: 0;
  top: 0;
  font-size: 0.8rem;
  background-color: #fff;
  height: 5vh;
}

textarea {
  resize: none;
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.stop-scrolling {
  height: 100%;
  overflow: hidden;
}

.btn {
  background-color: #f98327;
  border: none;
  border-radius: 16px;
  cursor: pointer;
  transition: 0.5s all;
}

.btn:hover {
  background-color: #fd6e00;
  transform: scale(1.1);
}

a {
  color: #000;
 text-decoration: none;
}

.a1 {
  
  grid-area: a1;
}

.a2 {
  grid-area: a2;
}

.a3 {
  grid-area: a3;
}

.a4 {
  grid-area: a4;
}

.a5 {
  grid-area: a5;
}